.info-data{
    justify-content: space-between;
    font-size: 1.6rem;
    display: flex;
    flex-wrap: wrap;
}
.info-data__back, .info-data__print{
    position: relative;
    top: 1rem;
    width: fit-content;
}
/*HOJA-----------------------------------------------------------------*/
.Hoja{
    margin: 2rem;
    display: flex;
    background-color: #fff;
    width: 100%;
}
.hoja__list{
    width: 90%;
    position: relative;
    right: 10px;
    list-style-type: none;
}
.hoja__item{
    display: flex;
    align-items: center;
    justify-content: space-between;
    line-height: 0;
    width: 100%;
    padding: .3rem;
    /* background-color: red; */
}
.hoja__item:nth-child(2n + 1){
    background-color: #f3f6f2;
}
.hoja__item:nth-child(2n + 2){
    background-color: #f0e9f2;
}