:root{
  --colorAzulBase: #273564;
}
html{
  font-size: 10px;
}
body {
  margin:0;
  width: 100%;
}
b{
  color: rgb(70, 70, 224);
  font-weight: 700;
  font-size: 1.2rem;
}
.boton, .delete-btn{
  padding: 1rem 2rem 1rem 2rem;
    font-size: 1.6rem;
    border: none;
    background-color: #1cdf60;
    border-radius: 5px;
    font-weight: 600;
    color: #fff;
}
.delete-btn{
  background-color: #fd2323;
  position: relative;
  left: 10%;
}
.boton:hover{
  background-color: #adf5cd;
  color: #33781d;
  cursor: pointer;
}
.delete-btn:hover{
  background-color: #fa9d9d;
  color: #cc2323;
}

.back-default{
  background-color: rgba(255, 255, 255, 0.5);
  /* font-size: 2rem; */
  border-radius: 1rem;
  box-shadow: 0px 0px 5px 3px rgba(0,0,0,.3);
  padding: 1rem;
}
/*ICONO de consulta*/
.search-icon{
  min-height: 1rem;
  max-height: 1rem;
  width: 1rem;
  margin-bottom: .5rem;
  border-radius: 50%;
  border: 4px solid #fff;
}
.search-icon::after{
  content: "";
  display: block;
  background-color: #fff;
  position: relative;
  height: 4px;
  width: 80%;
  top: 1.2rem;
  left: .8rem;
  transform: rotate(54deg);
}
