.operator-modifer{
    grid-column: 1/3;
    grid-row: 2/3;
    margin-bottom: 1rem;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}
.operator-modifer__result{
    min-width: 400px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
/*FORMULARIO*/
.operator-modifer__content{
    min-width: 430px;
    box-sizing: border-box;
    padding: 1rem;
    text-align: center;
}
#operator-modifer__form{
    width: 100%;
}
.operator-modifer__form-list{
    list-style: none;   
    width: 100%;
    box-sizing: border-box;
}
.operator-modifer__item{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 2rem 0 2rem 0;
    
}
.operator-modifer__item:last-child{
    display: flex;
    justify-content: space-between;
}
.operator-modifer__field{
    width: 40%;
    min-width: fit-content;
    height: 3rem;
    border: 2px solid #888 ;
    border-radius: 1rem;
}
.operator-modifer__label{
    font-size: 1.5rem;
    font-weight: 600;
    position: relative;
    left: 10%;
}
